body {
    font-family: Arial, sans-serif;
    background-color: #e0f7fa; 
    display: flex;
    justify-content: center;
    height: 100vh;
    margin: 0;
    margin-top: 20px;
  }
  
  .App {
    background: #fff;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    width: 100vw;
    max-width: 600px;
    margin-top: 50px; 
  }
  
  h1 {
    font-size: 24px;
    margin-bottom: 20px;
    color: #333;
    text-align: center;
  }
  
  form {
    display: flex;
    margin-bottom: 20px;
  }
  
  input[type="text"] {
    flex: 1;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 4px 0 0 4px;
    outline: none;
  }
  
  button {
    padding: 10px;
    border: none;
    background: #0288d1; 
    color: white;
    border-radius: 0 4px 4px 0;
    cursor: pointer
  }  
  
  button:hover {
    background: #0277bd; 
  }
  
  ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  li {
    background: #fff;
    margin-bottom: 10px;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 4px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  li span {
    flex: 1;
  }
  
  li input[type="text"] {
    flex: 1;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 4px;
    outline: none;
  }
  
  li button {
    margin-left: 5px;
    padding: 5px 10px;
  }
  
  @media (max-width: 600px) {
    .App {
      width: 100%;
      padding: 10px;
    }
  
    form {
      flex-direction: column;
    }
  
    input[type="text"], button {
      width: 100%;
      margin-bottom: 10px;
      border-radius: 4px;
    }
  
    li {
      flex-direction: column;
      align-items: flex-start;
    }
  
    li button {
      width: 100%;
      margin-left: 0;
      margin-bottom: 5px;
    }
  }
  